


















































import { Component, Vue } from 'vue-property-decorator';
import { Company as CompanyType } from '@/datatypes';

@Component
export default class Company extends Vue {
  get companies(): { previous: CompanyType, current: CompanyType, next: CompanyType } {
    const sortedCompanies =
      [...this.$store.state.companies].sort((a, b) => a.name - b.name);

    const { previous, current, next } = sortedCompanies.reduce((collector, c) =>
      collector.next
      // If we've found all - fall through
      ? collector
      : collector.current
      // If the company is already found collect the following one
      ? { next: c, ...collector }
      : c.id === this.$route.params.id
      // If this is the company store it in our collection
      ? { current: c, ...collector }
      // This company is potentially the previous one
      : { previous: c },
      {},
    );

    return current
      ? { previous: previous || {}, current, next: next || {} }
      : { next: {}, current: { name: 'loading...' }, previous: {} };
  }
  get company() {
    return this.companies.current;
  }

  private mounted() {
    const app = document.getElementById('app');
    if (app instanceof HTMLDivElement) {
      app.scrollTo(0, 0);
    }
  }
}
