




import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import { Company } from '@/datatypes';

const textSvg = (text: string) => new DOMParser().parseFromString(`
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120 40">
    <text x="50%" y="50%" dominant-baseline="middle" text-anchor="middle">
      ${text}
    </text>
  </svg>
`, 'text/xml');
const loadingSvg = textSvg('Loading...');
const errorSvg = textSvg('Failed to load');

function replaceWith(oldEl: Node, newEl: Node) {
  if (oldEl && oldEl.parentElement) {
    oldEl.parentElement.replaceChild(newEl, oldEl);
  }
}

const fetchSVG = async (src: string) => new DOMParser().parseFromString(
  await (await fetch(src)).text(),
  'text/xml',
);

function cloneXmlDocument(original: XMLDocument): XMLDocument {
  return <any>original.cloneNode(true);
}


function removeExtraTitle(el: Element) {
  const t = el.querySelector('circle title');
  if (t && t.parentElement) {
    t.parentElement.removeChild(t);
  }
}
function setTitle(el: Element, text: string) {
  const title = el.querySelector('title');
  if (title) {
    title.textContent = text;
  }
}
function wrapInSvgAnchor(el: Element, href: string) {
  const a = document.createElementNS('http://www.w3.org/2000/svg', 'a');
  a.setAttribute('href', href);
  replaceWith(el, a);
  a.appendChild(el);
}

const fixupBooth =
  (target: string, companies: Map<string, Company>) =>
  (el: Element) => {
  const c = companies.get(el.id.replace('booth-', ''));
  if (!c) {
    return;
  }
  if (el.id === target) {
    el.classList.add('target');
  }
  removeExtraTitle(el);
  setTitle(el, c.name);
  wrapInSvgAnchor(el, `/company/${c.id}`);
};

const mutateSvgBooths = (svg: XMLDocument, fn: ((el: Element) => void)) => {
  Array.from(svg.getElementsByClassName('booth')).forEach(fn);
  return svg;
};

Component.registerHooks([ 'beforeRouteUpdate' ]);
@Component
export default class SvgMap extends Vue {
  private baseSvg: XMLDocument = loadingSvg;

  @Watch('src')
  @Prop({ required: true }) public readonly src: string | undefined;
  public mounted() {
    console.log();
    this.loadSvg(this.src);
  }
  public onSrcChanged(newSrc: string, oldSrc: string) {
    this.loadSvg(newSrc);
  }

  public beforeRouteUpdate() {
    // XXX: This forces an unneccesary refetch of the svg, but works alright
    //      since the browser is caching stuff.
    this.loadSvg(this.src);
  }

  private setSvg(newSvg: XMLDocument) {
    const current = this.$el.querySelector('svg');
    const replacement = newSvg.querySelector('svg');
    if (current && replacement) {
      replaceWith(current, replacement);
    }
  }

  get companies(): Company[] {
    return this.$store.state.companies;
  }

  get svg() {
    const companies =
      this.companies.reduce((map, c) => map.set(c.booth, c), new Map());

    // XXX: We're mutating the same over and over
    const svgDoc = mutateSvgBooths(
      cloneXmlDocument(this.baseSvg),
      fixupBooth(this.$route.hash.substr(1), companies),
    );

    if (svgDoc === null) {
      console.error('Error in parsing SVG!');
      return;
    }

    const queriedSvg = svgDoc.querySelector('svg')

    if(queriedSvg === null) {
      console.error('Error in parsing SVG!');
      return;
    }

    return queriedSvg.outerHTML;
  }

  private async loadSvg(src: string | undefined) {
    if (!src) {
      return;
    }

    try {
      this.baseSvg = await fetchSVG(src);
    } catch (e) {
      this.baseSvg = errorSvg;
    }
  }
}
